@import '../../styles/commun.scss';

.containerCardMission{
  display: flex;
  flex-wrap: wrap;
  justify-content:space-around;
   background-color: #1f5da6;
   padding: 20px;
}

.thankyou_containerMission {
  display: grid;
  place-items: center;
  font-family: cursive;
  //width: 100vw;
  //height: 100vh;
 
}

.mainMission {
  width: 300px;
  height: 450px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
  border-radius: 20px;
  margin: 5px;
}

.mainMission .textMission {
  padding: 0px 60px 60px 60px;
  text-align: center;
  font-size: 0.8rem;
  color: #c73429;
}

.mainMission .svg {
  padding-top: 50px;
  transform-style: preserve-3d;
  transform: perspective(600px) rotateY(00deg);
  position: relative;
  animation: justAnimate 2s ease-in-out infinite;
}

@keyframes justAnimate {
  0% {
    transform: rotateY(0deg);
  }
  50% {
    transform: rotateY(40deg);
  }
  100% {
    transform: rotateY(0deg);
  }
}

